$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}Promo {
    position: absolute;
    padding: 240px 128px 0;

    width: 100%;
    height: 100%;

    box-sizing: border-box;

    display: flex;
    flex-direction: column;

    transition: background-color ease-in-out 1s;

    &_type-1 {
        background-color: #140827;
    }

    &_type-2 {
        background-color: #071B29;
    }

    &_type-3 {
        background-color: #2D0E26;
    }

    &_type-4 {
        background-color: #092629;
    }

    &__title {
        width: 1010px;
        font-weight: 800;
        font-size: 80px;
        line-height: 88px;
        color: #fff;

        z-index: 10;
    }

    &__subtitle {
        width: 958px;
        margin-top: 32px;
        font-weight: 600;
        font-size: 40px;
        line-height: 48px;
        color: rgba(255, 255, 255, 0.74);

        z-index: 10;
    }

    &__button-container {
        margin-top: 64px;
        display: flex;
    }

    &__button {
        margin-right: 34px;

        z-index: 10;

        transition: background-color .3s ease-in-out;

        &:focus {
            background: #8238cc;
            transform: scale(1.125);
        }
    }

    &__image {
        position: absolute;

        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        opacity: 1;

        z-index: 10;

        transition: opacity ease-in-out 1s;

        &_hidden {
            opacity: 0;
        }

        &_type-1 {
            top: 162px;
            right: 154px;
            width: 614px;
            height: 783px;
            background-image: url("./popcorn-1.png");
        }

        &_type-2 {
            top: 168px;
            right: 66px;
            height: 788px;
            width: 816px;

            background-image: url("./popcorn-2.png");
        }

        &_type-3 {
            top: 216px;
            right: 154px;
            height: 710px;
            width: 614px;

            background-image: url("./popcorn-3.png");
        }

        &_type-4 {
            top: 164px;
            right: 63px;
            height: 788px;
            width: 816px;

            background-image: url("./popcorn-4.png");
        }
    }

    &__glow {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        overflow: hidden;

        z-index: 0;
    }

    &__glow-1 {
        position: absolute;
        width: 751.18px;
        height: 870.3px;
        left: 175px;
        top: 350px;


        mix-blend-mode: normal;
        opacity: 0.4;
        filter: blur(260px);
        transform: rotate(45deg);

        &_type-1 {
            background: #5E008A;
        }

        &_type-2 {
            background: #006E8A;
        }

        &_type-3 {
            background: #87008A;
        }

        &_type-4 {
            background: #008A48;
        }
    }

    &__glow-2 {
        position: absolute;
        width: 528px;
        height: 408px;
        top: 220px;
        left: 970px;

        filter: blur(350px);
        transform: rotate(-7.84deg);

        &_type-1 {
            background: #5302A4;
        }

        &_type-2 {
            background: #02A46C;
        }

        &_type-3 {
            background: #A40294;
        }

        &_type-4 {
            background: #1DA833;
        }
    }

    &__glow-3 {
        position: absolute;
        width: 320px;
        height: 272px;
        top: 470px;
        left: 660px;

        filter: blur(350px);

        &_type-1 {
            background: #4000A9;
        }

        &_type-2 {
            background: #00A91C;
        }

        &_type-3 {
            background: #A90084;
        }

        &_type-4 {
            background: #379246;
        }
    }

    &__glow-4 {
        position: absolute;
        width: 662px;
        height: 562px;
        left: 832px;
        top: 216px;

        filter: blur(350px);

        &_type-1 {
            background: rgba(153, 80, 188, 0.3);
        }

        &_type-2 {
            background: rgba(80, 166, 188, 0.3);
        }

        &_type-3 {
            background: rgba(188, 80, 164, 0.3);
        }

        &_type-4 {
            background: rgba(80, 188, 110, 0.3);
        }
    }
}
