$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}HeaderAddress {
    display: flex;
    align-items: center;

    text-decoration: none;
    margin-left: 8px;

    border-radius: 40px;
    padding: 20px 40px;

    height: 80px;
    box-sizing: border-box;

    transition: .2s ease-in-out;
    will-change: background-color;

    &:focus {
        background: #08A652;
    }

    &__icon {
        width: 32px;
        height: 38px;

        background-image: url('./address.svg');
        background-repeat: no-repeat;
        background-size: contain;
    }

    &__text {
        margin-left: 20px;
        height: 40px;
        line-height: 40px;
        color: #fff;
        font-size: 32px;
        font-weight: 600;
    }
}
