$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}Header {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    box-sizing: border-box;

    --spatial-navigation-contain: contain;

    height: 160px;

    padding: 60px 88px 28px 128px;

    &__back {
        position: absolute;
        top: 78px;
        left: 56px;

        width: 20px;
        height: 36px;

        background-image: url('./chevron-right.svg');
        background-repeat: no-repeat;
        background-size: contain;

        transition: transform .2s ease-in-out;
    }

    &__logo {
        width: 72px;
        height: 72px;
        border-radius: 8px;

        background-image: url('./logo.svg');
        background-repeat: no-repeat;
        background-size: contain;
    }

    &__title {
        display: inline-block;
        margin-left: 24px;
        height: 48px;
        line-height: 44px;
        font-size: 40px;
        font-weight: bold;
        color: #fff;
    }

    &__section {
        display: flex;
        align-items: center;
    }

    &__pencil {
        width: 24px;
        height: 24px;
        margin-right: 10px;

        background-image: url('./pencil.svg');
        background-repeat: no-repeat;
        background-size: contain;
    }
}
