$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}NoArea {
    height: 100%;
    position: relative;
    background: #001124;;

    &__header {
        position: relative;

        z-index: 10;
    }

    &__container {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        padding: 64px 0;

        z-index: 10;
    }

    &__badge {
        display: flex;
        flex-direction: column;

        align-items: center;

        width: 132px;
        height: 124px;
    }

    &__badge-body {
        width: 84px;
        height: 84px;
        border-radius: 50%;

        background-image: url("./face.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }

    &__badge-name {
        color: #fff;
        height: 34px;
        font-size: 32px;
        font-weight: 600;
        line-height: 34px;
        text-align: center;
    }

    &__badge-desc {
        height: 20px;
        color: rgba(255, 255, 255, 0.8);
        font-size: 24px;
        font-weight: 500;
        line-height: 20px;
        text-align: center;
    }

    &__badge-kernel {
        height: 36px;
        width: 4px;

        background: #E12D39;
        border-bottom-left-radius: 2px;
        border-bottom-right-radius: 2px;
    }

    &__badge-shadow_big {
        margin-top: -2px;

        width: 74px;
        height: 6px;
        background: #06182E;
        filter: blur(6px);

        display: flex;
        justify-content: center;
    }

    &__badge-shadow_small {
        margin-top: -4px;
        width: 50px;
        height: 4px;
        background: #06182E;
        filter: blur(4px);
    }

    &__title {
        margin-top: 48px;
        font-weight: bold;
        font-size: 72px;
        line-height: 72px;
        color: #fff;
    }

    &__subtitle {
        margin-top: 12px;
        font-weight: 500;
        font-size: 32px;
        line-height: 40px;
        color: #fff;
    }

    &__button {
        margin-top: 40px;
    }

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: url("../../assets/images/map.png");
        background-blend-mode: luminosity;

        z-index: 1;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;

            background: linear-gradient(0, #001124 0, rgba(0, 17, 36, 0) 65%);
            transform: rotate(-180deg);
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;

            background: linear-gradient(0, #001124 0, rgba(0, 17, 36, 0) 65%);
        }
    }
}
