$ns: "fdt-";
/**
 * Fonts
 */
@import './SBSansText.scss';
@import './SBSansDisplay.scss';

$base-width: 960;
$base-height: 540;

$background-color: #001124;

$sc-accent-green: #08a652;
$sc-green: #34b76f;
$sc-red: #ff486f;

$unit: 4px;

$def-border-radius: 8px;
$def-transition-options: .2s ease-in;

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
}

* {
    outline: none;
}

a {
    text-decoration: none;

    color: none;
}

a:visited {
    text-decoration: none;
}

a:focus {
    outline: none;
}
